export default {
	/**
	 * 数据
	 */

	props: {
		// 是否禁用
		disabled: {
			type: Boolean,
			default: false
		},

		// placeholder
		placeholder: {
			type: String,
			default: '请选择日期时间'
		},

		// 表示有效日期时间范围的开始，字符串格式为"YYYY-MM-DD hh:mm"
		start: {
			type: String,
			default: '1970-01-01 00:00'
		},

		// 表示有效日期时间范围的结束，字符串格式为"YYYY-MM-DD hh:mm"
		end: {
			type: String,
			default: '2300-01-01 00:00'
		},

		// 有效值 year,month,day,hour,minute，表示选择器的粒度
		fields: {
			type: String,
			default: 'minute'
		},

		// 默认值
		defaultValue: {
			type: [Date, String],
			default() {
				return null;
			}
		}
	},

	/**
	 * 数据
	 */

	data() {
		return {
			range: [],
			value: [],
			dateStr: '' // 最终显示的文字
		};
	},

	/**
	 * 组件初次加载完成
	 */
	onReady() {
		// 设置默认值
		if (this.defaultValue) {
			// 设置显示的值
			this.setDateStr(new Date(this.defaultValue));
		}
		
		// 设置range
		this.setRange();
		
		// 设置默认value
		this.setDefaultValue();
	},

	/**
	 * 方法
	 */
	methods: {
		/**
		 * 确认选择
		 */
		change(event) {},

		/**
		 * 设置显示的值
		 * @param {Date|String} date 日期字符串或日期对象
		 */
		setDateStr(date) {
			// 格式化日期
			let dt = this.$utils.formatDate(date);
			console.log(dt);

			// 根据选择器粒度设置不同的值
			if (this.fields == 'year') {
				this.dateStr = dt.YYYY;
			} else if (this.fields == 'month') {
				this.dateStr = dt.YYYY + '-' + dt.MM;
			} else if (this.fields == 'day') {
				this.dateStr = dt.YYYY + '-' + dt.MM + '-' + dt.DD;
			} else if (this.fields == 'hour') {
				this.dateStr = dt.YYYY + '-' + dt.MM + '-' + dt.DD + ' ' + dt.hh;
			} else if (this.fields == 'minute') {
				this.dateStr = dt.YYYY + '-' + dt.MM + '-' + dt.DD + ' ' + dt.hh + ':' + dt.mm;
			}
		},
		
		/**
		 * 设置range
		 */ 
		setRange() {
			// 有效日期
			let dateStart = new Date(this.start); // 有效日期开始
			let dateEnd = new Date(this.end); // 有效日期结束
			
			// 判断有效日期结束是否大于有效日期开始，如果不是，则将有效日期结束修改为有效日期开始往后300年
			if(dateEnd <= dateStart) {
				dateEnd = new Date(this.start);
				dateEnd.setFullYear(dateStart.getFullYear() + 300);
				dateEnd.setDate(dateEnd.getDate() - 1);
			}
			
			// range column 值
			let rangeYear = [];
			let rangeMonth = [];
			let rangeDay = [];
			let rangeHour = [];
			let rangeMinute = [];
			
			// 得到 rangeYear
			let yearStart = dateStart.getFullYear(); // rangeYear 开始
			let yearEnd = dateEnd.getFullYear(); // rangeYear 结束
			for(let year = yearStart; year <= yearEnd; year++) {
				rangeYear.push(year + '年');
			}
			
			// 得到 rangeMonth
			let monthStart = dateStart.getMonth() + 1; // rangeMonth 开始
			let monthEnd = yearEnd == yearStart ? dateEnd.getMonth() + 1 : 12; // rangeMonth 结束
			for(let month = monthStart; month <= monthEnd; month++) {
				rangeMonth.push(month + '月');
			}
			
			// 得到 rangeDay
			let dayStart = dateStart.getDate(); // rangeDay 开始
			let dayEnd = (yearEnd == yearStart && monthEnd == monthStart) ? dateEnd.getDate() : (new Date(yearEnd, monthEnd, 0)).getDate(); // rangeDay 结束
			for(let day = dayStart; day <= dayEnd; day++) {
				rangeDay.push(day + '日');
			}
			
			// 得到 rangeHour
			let hourStart = dateStart.getHours(); // rangeHour 开始
			let hourEnd = (yearEnd == yearStart && monthEnd == monthStart && dayEnd == dayStart) ? dateEnd.getHours() : 24; // rangeHour 结束
			for(let hour = hourStart; hour <= hourEnd; hour++) {
				rangeHour.push(hour + '时');
			}
			
			// 得到 rangeMinute
			let minuteStart = dateStart.getMinutes(); // rangeMinute 开始
			let minuteEnd = (yearEnd == yearStart && monthEnd == monthStart && dayEnd == dayStart && hourEnd == hourStart) ? dateEnd.getMinutes() : 59; // rangeMinute 结束
			for(let minute = minuteStart; minute <= minuteEnd; minute++) {
				rangeMinute.push(minute + '分');
			}
			
			console.log('rangeYear：', rangeYear);
			console.log('rangeMonth：', rangeMonth);
			console.log('rangeDay：', rangeDay);
			console.log('rangeHour：', rangeHour);
			console.log('rangeMinute：', rangeMinute);
			
			// 根据选择器粒度设置不同的range值
			let range = [];
			if (this.fields == 'year') {
				range = [rangeYear];
			} else if (this.fields == 'month') {
				range = [rangeYear, rangeMonth];
			} else if (this.fields == 'day') {
				range = [rangeYear, rangeMonth, rangeDay];
			} else if (this.fields == 'hour') {
				range = [rangeYear, rangeMonth, rangeDay, rangeHour];
			} else if (this.fields == 'minute') {
				range = [rangeYear, rangeMonth, rangeDay, rangeHour, rangeMinute];
			}
			this.range = range;
			console.log('range', range);
		},
		
		/**
		 * 设置默认 value
		 */ 
		setDefaultValue() {
			let dateDefault = null; // 默认日期
			let dateStart = new Date(this.start); // 有效开始日期
			let dateEnd = new Date(this.end); // 有效结束日期
			
			// 设置默认日期
			// 如果没有设置默认日期，将默认日期设置为当前日期
			// 如果有设置默认日期，但是默认日期不在有效日期范围内，将默认日期设置为当前日期
			// 如果有设置默认日期，默认日期不在有效日期范围内，当前日期也不在有效日期范围内，设置默认日期为有效日期开始值
			if(!this.defaultValue) {
				dateDefault = new Date();
			} else {
				dateDefault = new Date(this.defaultValue);
			}
			if(dateDefault < dateStart || dateDefault > dateEnd) {
				dateDefault = new Date(this.start);
			}
			
			console.log('@@',dateDefault)
			console.log('@@',dateStart)
			console.log('@@',dateEnd)
			
			// 初始化 value 值
			let value = []; 
			
			// 根据选择器粒度执行不同的 value 值
			if (this.fields == 'year') {
				let indexYear = this.range[0].findIndex(year => {
					return parseInt(year) == dateDefault.getFullYear();
				});
				this.value = [indexYear];
			} else if (this.fields == 'month') {
				let indexYear = this.range[0].findIndex(year => {
					return parseInt(year) == dateDefault.getFullYear();
				});
				let indexMonth = this.range[1].findIndex(month => {
					return parseInt(month) == dateDefault.getMonth() + 1;
				});
				this.value = [indexYear, indexMonth];
			} else if (this.fields == 'day') {
				let indexYear = this.range[0].findIndex(year => {
					return parseInt(year) == dateDefault.getFullYear();
				});
				let indexMonth = this.range[1].findIndex(month => {
					return parseInt(month) == dateDefault.getMonth() + 1;
				});
				let indexDay = this.range[2].findIndex(day => {
					return parseInt(day) == dateDefault.getDate();
				});
				this.value = [indexYear, indexMonth, indexDay];
			} else if (this.fields == 'hour') {
				let indexYear = this.range[0].findIndex(year => {
					return parseInt(year) == dateDefault.getFullYear();
				});
				let indexMonth = this.range[1].findIndex(month => {
					return parseInt(month) == dateDefault.getMonth() + 1;
				});
				let indexDay = this.range[2].findIndex(day => {
					return parseInt(day) == dateDefault.getDate();
				});
				let indexHour = this.range[3].findIndex(hour => {
					return parseInt(hour) == dateDefault.getHours();
				});
				this.value = [indexYear, indexMonth, indexDay, indexHour];
			} else if (this.fields == 'minute') {
				let indexYear = this.range[0].findIndex(year => {
					return parseInt(year) == dateDefault.getFullYear();
				});
				let indexMonth = this.range[1].findIndex(month => {
					return parseInt(month) == dateDefault.getMonth() + 1;
				});
				let indexDay = this.range[2].findIndex(day => {
					return parseInt(day) == dateDefault.getDate();
				});
				let indexHour = this.range[3].findIndex(hour => {
					return parseInt(hour) == dateDefault.getHours();
				});
				let indexMinute = this.range[4].findIndex(minute => {
					return parseInt(minute) == dateDefault.getMinutes();
				});
				this.value = [indexYear, indexMonth, indexDay, indexHour, indexMinute];
			}
			
			console.log('@',this.value)

			// 更新 dateStr
			this.setDateStr(dateDefault);
		},
		
		/**
		 * 某一列的值改变时触发
		 * @param {Number} event.detail.column 表示改变了第几列（下标从0开始）
		 * @param {Number} event.detail.value 表示变更值的下标
		 */
		columnchange(event) {
			let columnIndex = event.detail.column; // 改变的列的下标
			let valueIndex = event.detail.value; // 变更值的下标
			
			// 改变的是 Year 这一列
			if(columnIndex == 0) {
				let dateStart = new Date(this.start); // 有效日期开始
				let year = this.range[columnIndex][valueIndex]; // 改变后的年
				
				// 根据选择器粒度执行不同的逻辑
				if (this.fields == 'year') {
					// 更新 value
					this.value = [valueIndex];
				} else if (this.fields == 'month') {
					range = [rangeYear, rangeMonth];
				} else if (this.fields == 'day') {
					range = [rangeYear, rangeMonth, rangeDay];
				} else if (this.fields == 'hour') {
					range = [rangeYear, rangeMonth, rangeDay, rangeHour];
				} else if (this.fields == 'minute') {
					range = [rangeYear, rangeMonth, rangeDay, rangeHour, rangeMinute];
				}
			}
			// 改变的是 Month 这一列
			else if (columnIndex == 1) {
				
			}
			// 改变的是 Day 这一列
			else if(columnIndex == 2) {
				
			}
			// 改变的是 hour 这一列
			else if(columnIndex == 3) {
				
			}
			// 改变的是 Minute 这一列
			else if(columnIndex == 4) {
				
			}
		},
	}
};
